export default {

	data() {
		return {
			indicatorDots: true, //是否显示面板指示点
			autoplay: true, //是否自动切换
			interval: 10000, //自动切换时间间隔
			duration: 1000, //	滑动动画时长1s
			circular: true, //是否衔接滑动
			ad: [],
			inputShowed: false,
			inputVal: "",
			balance: 0,
			isShow: false,
			messages: "",
			id: 0,
			title: "",
		}
	},
	onLoad: function() {
		// let url = window.location.href; //获取URL地址 
		let url = 'ocDgH0VJLfQSaGuK89YtRPmmgeTo'
		all.globalData.openid = url

		// 获取广告
		request.req({
			url: cfg.qjUrl + 'ad/list',
			method: 'get',
			data: {},
			success: (res) => {
				if (res.statusCode == 200) {
					console.log('req result', res);
					this.ad = res.data
					all.globalData.ad = res.data;
					console.log('this.data.ad', this.ad);
					console.log('globalData.ad', all.globalData.ad);
				} else {
					console.log("网络访问错误！检查服务器是否正常！")
				}
			},
			fail: function(res) {
				console.log('网络访问失败！')
			}
		});

		//获取当前余额
		request.req({
			url: cfg.qjUrl + 'balance/balance',
			method: 'get',
			data: {},
			success: (res) => {
				console.log("余额", res.data)
				all.globalData.balance = res.data.balance
				this.balance=all.globalData.balance
				
			}
		});


		//获取公告
		request.req({
			url: cfg.qjUrl + 'notice/notice',
			method: 'GET',
			data: {},
			success: (res) => {
				console.log("qqq:", res.data)
				if (!res.data.ok) {
					console.log("公告:", res.data)
					this.messages=res.data.notice.brief
						this.isShow=true,
						this.id=res.data.notice.id
						this.title=res.data.notice.title
					
				}

			}
		})
		
				// 获取金币数量   
		request.req({
			url: cfg.qjUrl + 'user/gold/number',
			method: 'get',
			data: {},
			success: (res) => {
				if (res.statusCode == 200) {
					console.log('user/gold/number req result', res);
					all.globalData.user = res.data;
					uni.setStorageSync("user", res.data)
					if (res.data.balance) {
						this.gold = res.data.gold,
							this.alance = res.data.balance
					} else if (res.data.ok = 2) {
						console.log("用户没找到");
					} else if (res.data.ok = 1) {
						console.log("服务器异常错误");
					} else {
						console.log("其他错误，未获得gold值");
					}
					console.log("余额", all.globalData.balance)
				} else {
					console.log("网络访问错误！检查服务器是否正常！")
				}
			},
			fail: function(res) {
				console.log('网络访问失败！')
			}
		})
	},
	onShow: function(options) {

	},

	methods: {
