		//广告点击跳转
		changeTo: (event) => {
			console.log(event);
			let id = event.currentTarget.dataset.id; //广告在globalData.ad中的index
			uni.navigateTo({
				url: `/pages/showAD/showAD?id=${id}` //把广告在globalData.ad中的index传给跳转的page
			});
		},

		//“咨询”按钮跳转
		jumpToAsk() {
			// 验证是否已登录
			user.isLogin(
				function(res) {
					console.log("jump to page Ask")
					if (!all.globalData.isTested) {
						uni.showModal({
							title: "您还未完成形象测试！",
							content: "建议您先完成形象测试，我们只有根据形象测试结果才能对您的咨询问题给出最好的搭配方案。",
							showCancel: false,
							confirmText: "确定",
							success: function(res) {
								if (res.confirm) {
									uni.navigateTo({
										url: '../ask/ask'
									})
								}
							}
						})
					} else {
						uni.navigateTo({
							url: '../ask/ask'
						})
					}
				},
				function(res) {
					console.log("用户未授权或登录")
				}
			)
		},
		//“找顾问”按钮跳转
		jumpToFindadvisor() {
			// 验证是否已登录
