							showCancel: false,
							confirmText: "确定",
							success: function(res) {
								if (res.confirm) {
									uni.switchTab({
										url: '/pages/consult/consult',
									})
								}
							}
						})
					} else {
						//查询形象测试优惠券   
						request.req({
							url: cfg.qjUrl + 'coupon/list/11',
							method: 'GET',
							data: {},
							success: (res) => {
								console.log("111", res.data)
								if (res.statusCode == 200) {
									console.log('req result', res);
									if (res.data.length == 0) { // 如果没有形象测试优惠券
										uni.showModal({
											title: "缺少优惠券！",
											content: "您没有个人形象测试专项优惠券，无法进行测试！如想继续测试，请与奇纪客服联系。",
											showCancel: false,
											confirmText: "确定",
											success: function(res) {
												if (res.confirm) {
													uni.switchTab({
														url: '/pages/consult/consult',
													})
												}
											}
										})
									} else {
										console.log("jump to page imageTest")
										uni.navigateTo({
											url: '../imageTest/imageTest'
										})
									}
								} else {
									console.log("网络访问错误！检查服务器是否正常！")
								}
							},
							fail: function(res) {
								console.log('网络访问失败！')
							}
						})
					}
				},
				function(res) {
					console.log("用户未授权或登录")
				}
			)
		},
		//“私人衣橱”按钮跳转
		jumpToWardrobe() {
			// 验证是否已登录
			user.isLogin(
				function(res) {
